import React, { useState } from 'react'

const VisibleText = ({ children }) => {
  const [show, setShow] = useState(false)

  if (show) {
    return (
      <>
        <button type="button" className="btn shadow-none" style={{ outline: 'None' }} onClick={() => setShow(!show)}>‚¨ÜÔ∏è</button>
        {children}
      </>
    )
  }

  return (<button type="button" className="btn shadow-none" style={{ outline: 'None' }} onClick={() => setShow(!show)}>‚¨áÔ∏è</button>)
}

export default VisibleText